<template>
  <div>
    <div class="lds-dual-ring" v-if="photos === []"></div>
    <div class="photo-container">
      <transition-group name="tile" tag="masonry">
        <div class="photo-item" v-for="photo in photos" :key="photo.id">
          <img :src="photo.urls.small" :alt="photo.alt_description" />
          <div class="photo-desc">
            <span>{{
              photo.description || photo.alt_description || "No description"
            }}</span>
          </div>
        </div>
      </transition-group>
    </div>
    <infinite-loading @infinite="infiniteHandler"></infinite-loading>
  </div>
</template>

<script>
import InfiniteLoading from "vue-infinite-loading";

export default {
  data() {
    return {};
  },
  components: {
    InfiniteLoading
  },
  methods: {
    infiniteHandler($state) {
      this.$store.dispatch("GET_PHOTOS");
      setTimeout(() => $state.loaded(), 500);
    }
  },
  computed: {
    photos() {
      return this.$store.state.photos;
    }
  }
};
</script>

